import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux'
import { fetchGroup, removeGroup, addGroup } from './actions/actions.js'
import { Link } from 'react-router-dom'
import "@fortawesome/fontawesome-free"


import "./style.css"

function App({fetchGroup, getData, removeGroup, addGroup}) {

  useEffect(()=>{
    fetchGroup()
  }, [])

  const [group, setGroup] = useState('')
  const [faculty, setFaculty] = useState('')
  

  const handleChangeGroup = event => setGroup(event.target.value)
  const handleChangeFaculty = event => setFaculty(event.target.value)


  const handleClick = () => {
    if(group.length == 3 && faculty) {
      const obj = { group: group, course: group[0], faculty: faculty}
      addGroup(obj)
      setGroup('')
      setFaculty('')
    }
  }

  return (
    <div className = "app">
      <p className = "title"> Список групп </p>
      <ul className="ul">
          {getData.map(i => 
            <li className = "li" key = {i.id}>
              <Link className = "link" to = {{ pathname: `/group/${i.id}`, fromDashboard: false}}>
                  {i.number}
              </Link>
              <button onClick = {()=>removeGroup(i.id)}>Remove</button>
            </li>)
          }
      </ul>

      <h1>ADD GROUP</h1>
      <input value = {group} onChange = {handleChangeGroup}  placeholder = "enter group"/>
      <input value = {faculty} onChange = {handleChangeFaculty} placeholder = "enter fuculty" />
      <button type = "button" onClick = {handleClick} />
    </div>
  );
}


const mapStateToProps = state => ({
  getData: state.getData
})


const mapDispatchToProps = {fetchGroup, removeGroup}

export default connect(mapStateToProps, mapDispatchToProps)(App);
